.header {
  padding: 20px 0;
  .container {
    max-width: 1660px;
  }
  &__location {
    font-size: 16px;
    line-height: 100%;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 25px;
    li a {
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
      &:hover {
        color: #ED1C24;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 25px;
  }
  &__email {
    font-size: 16px;
    line-height: 100%;
    text-decoration: underline;
    &:hover {
      text-decoration: unset;
      color: #ED1C24;
    }
  }
  &__phone {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 20px;
    line-height: 100%;
    font-weight: 500;
    &:hover {
      color: #ED1C24;
    }
    i {
      width: 22px;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
  }
  &__logo {
    width: 251px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group2 {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__catalog {
    &-show {
      height: 60px;
      padding: 5px 40px;
      span {
        font-size: 20px;
        text-transform: uppercase;
      }
    }
  }
  &__search {
    position: relative;
    flex-grow: 1;
    input {
      width: 100%;
      height: 60px;
      background: transparent;
      border: 1px solid #414042;
      border-radius: 20px;
      padding: 0 60px 0 40px;
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
      color: #414042;
      &::placeholder {
        color: #414042;
      }
    }
    &-btn {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      width: 30px;
      background: transparent;
      padding: 0;
      img {
        width: 100%;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: stretch;
    gap: 20px;
  }
  &__btn {
    height: 60px;
    padding: 5px 30px;
    span {
      font-size: 20px;
      font-weight: 500;
    }
  }
  &__text {
    text-align: center;
    font-size: 16px;
    line-height: 100%;
  }
  .menu-bg {
    display: none;
  }
}
.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 25px;
}
.header-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  margin-top: 45px;
}
.header-bottom {
  margin-top: 20px;
}
.header-mob {
  display: none;
}
.burger {
  display: none;
}

@media screen and (max-width: 1600px) {
  .header {
    .container {
      max-width: 1260px;
    }
    &__location {
      font-size: 13px;
    }
    &__list {
      gap: 15px;
      li a {
        font-size: 16px;
      }
    }
    &__group {
      gap: 15px;
    }
    &__email {
      font-size: 13px;
    }
    &__phone {
      gap: 8px;
      font-size: 16px;
      i {
        width: 16px;
      }
    }
    &__logo {
      width: 200px;
    }
    &__group2 {
      gap: 28px;
    }
    &__catalog {
      &-show {
        height: 50px;
        padding: 5px 30px;
        span {
          font-size: 16px;
        }
      }
    }
    &__search {
      input {
        height: 50px;
        padding: 0 50px 0 20px;
        font-size: 16px;
      }
      &-btn {
        right: 15px;
        width: 20px;
      }
    }
    &__btn {
      height: 50px;
      padding: 5px 30px;
      span {
        font-size: 16px;
      }
    }
    &__text {
      font-size: 14px;
    }
  }
  .header-top {
    gap: 15px;
  }
  .header-center {
    gap: 28px;
    margin-top: 30px;
  }
  .header-bottom {
    margin-top: 15px;
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__list {
      li a {
        font-size: 13px;
      }
    }
    &__group {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
    &__phone {
      font-size: 13px;
      i {
        width: 12px;
      }
    }
    &__group2 {
      gap: 15px;
    }
    &__btns {
      gap: 15px;
    }
    &__btn {
      padding: 5px 20px;
    }
  }
  .header-center {
    gap: 30px;
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__location {
      max-width: 185px;
      font-size: 14px;
      margin-top: 20px;
    }
    &__list {
      flex-direction: column;
      align-items: start;
      gap: 25px;
      li a {
        font-size: 20px;
      }
    }
    &__group {
      align-items: center;
      text-align: center;
    }
    &__phone {
      order: -1;
      font-size: 16px;
      i {
        width: 16px;
      }
    }
    &__email {
      font-size: 16px;
    }
    &__group2 {
      flex-grow: unset;
    }
    &__search {
      flex-grow: unset;
      input {
        height: 40px;
        border-radius: 16px;
      }
    }
    &__catalog {
      &-show {
        width: 100%;
        span {
          font-size: 18px;
          text-transform: unset;
        }
      }
    }
    &__btns {
      flex-direction: column;
      gap: 10px;
    }
    &__btn {
      width: 100%;
      span {
        font-size: 18px;
      }
    }
    .menu-bg {
      position: fixed;
      z-index: 5;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      background: rgba(65, 64, 66, 0.5);
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
  .header-top {
    display: none;
  }
  .header-center {
    gap: 15px;
    margin-top: 0;
    .header__search,
    .header__btns {
      display: none;
    }
  }
  .header-mob {
    position: fixed;
    z-index: 10;
    top: 0;
    right: 0;
    transform: translateX(105%);
    width: 300px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 25px;
    background: #fff;
    padding: 80px 20px 60px;
    pointer-events: none;
    transition: all 0.3s;
    overflow-y: auto;
    &--active {
      transform: translateX(0);
      pointer-events: auto;
    }
  }
  .burger {
    position: relative;
    z-index: 12;
    width: 22px;
    height: 18px;
    display: block;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 40px;
      height: 40px;
      display: none;
      background: #fff;
      border: 1px solid #E0DFE1;
      border-radius: 50%;
    }
    span {
      position: absolute;
      z-index: 2;
      left: 0;
      width: 100%;
      height: 2px;
      background: #414042;
      border-radius: 3px;
      transition: all 0.3s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      &:after {
        display: block;
      }
      span:nth-child(1) {
        top: 8px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 8px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 140px;
    }
    &__text {
      font-size: 12px;
    }
  }
  .header-center {
    .header__catalog {
      &-show {
        height: 30px;
        border-radius: 10px;
        padding: 5px 20px;
        span {
          font-size: 14px;
          text-transform: uppercase;
        }
      }
    }
  }
}

@media screen and (max-width: 374px) {
  .header {
    .menu-bg {
      display: none;
    }
  }
  .header-mob {
    width: 100%;
  }
}
