.wrapper {
  overflow: hidden;
}
.btn-red {
  background: #ED1C24;
  border-radius: 20px;
  color: #fff;
  &:hover {
    background: #FA666C;
  }
  span {
    line-height: 100%;
    font-weight: 700;
  }
}
.btn-gray {
  background: #E0DFE1;
  border-radius: 20px;
  &:hover {
    background: #F4F4F4;
  }
  span {
    line-height: 100%;
    font-weight: 700;
  }
}
.btn-black {
  background: #414042;
  border-radius: 20px;
  color: #fff;
  &:hover {
    background: #747474;
  }
  span {
    line-height: 100%;
    font-weight: 700;
  }
}

@media screen and (max-width: 576px) {
  .btn-red,
  .btn-gray,
  .btn-black {
    border-radius: 16px;
  }
}